code {
  font-family: Menlo, Monaco, Consolas, "Courier New", monospace;
  background: #272822;
  color: #fff;
  padding: .125em .5em;
  border-radius: .25em;
  margin: 1px;
}

pre>code {
  display: block;
  overflow: auto;
}

code.block {
  display: block;
  overflow: auto;
  white-space: pre;
}