input,
.input,
textarea,
select,
button,
.button,
.input-group > span,
.button-group > span {
  max-width: 100%;
  height: 30px;
  line-height: 28px;
  padding: 0 0.5em;
  border: solid 1px #ddd;
  box-shadow: 0px 0px 0px 0px #ddd;
  border-radius: 3px;
  vertical-align: middle;
  background: #fff;
  transition: 0.3s border, box-shadow;
}
input:hover,
.input:hover,
textarea:hover,
select:hover,
button:hover,
.button:hover {
  border-color: #aaa;
  z-index: 1;
}
input:focus,
.input:focus,
textarea:focus,
select:focus,
button:focus,
.button:focus {
  border-color: #aaa;
  box-shadow: 0px 0px 5px #ddd;
  box-shadow: 0px 0px 4px 1px #ddd;
  z-index: 2;
}
input[disabled],
.input[disabled],
textarea[disabled],
select[disabled],
button[disabled],
.button[disabled] {
  background: #eee;
  cursor: default;
}
input,
.input,
textarea {
  width: 250px;
}
textarea {
  min-height: 30px;
  height: 60px;
  resize: vertical;
}
textarea[rows] {
  height: auto;
}
button,
.button {
  padding-left: 1em;
  padding-right: 1em;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
button:active,
.button:active {
  box-shadow: 0px 0px 5px #ddd inset;
  z-index: 3;
}
.input-group,
.button-group {
  display: inline-flex;
}
.input-group > *,
.button-group > * {
  position: relative;
  margin-right: -1px;
}
.input-group > *:not(:first-child),
.button-group > *:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.input-group > *:not(:last-child),
.button-group > *:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.input-group > select,
.button-group > select {
  flex: 1;
}
.input-group > input,
.button-group > input,
.input-group .input,
.button-group .input,
.input-group textarea,
.button-group textarea {
  flex: 99;
}
.input-group button,
.button-group button,
.input-group .button,
.button-group .button {
  flex: 1;
}
@supports (border-width: .5px) {
  input,
  .input,
  textarea,
  select,
  button,
  .button,
  .input-group > span,
  .button-group > span {
    border-width: 0.5px;
  }
}
