label {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
label > input[type="checkbox"],
label > input[type="radio"] {
  margin-right: 1ex;
}
.checkbox-group,
.radio-group {
  vertical-align: middle;
  margin-left: -1ex;
  margin-right: -1ex;
}
.checkbox-group > label,
.radio-group > label {
  display: inline-block;
  line-height: 30px;
  padding-left: 1ex;
  padding-right: 1ex;
}
select {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  padding-right: 1.5em;
  background-image: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="14" height="4"><path d="m 0 0, l 2.5 2.5, l 2.5 -2.5" style="fill:transparent;stroke:#aaa;stroke-width:1.125px" /></svg>');
